import React from "react";

class CowinAlert extends React.Component {
    
  render() {
    return (
      <div>
        Book appointment now - pin 380013
        <p />
        <a href="https://selfregistration.cowin.gov.in/">
          {" "}
          https://selfregistration.cowin.gov.in/
        </a>
      </div>
    );
  }

  async componentDidMount() {
    try {
        let counter = 0;
      setInterval(async () => {
        const res = await fetch(
          "https://cdn-api.co-vin.in/api/v2/appointment/sessions/calendarByPin?pincode=380013&date=14-05-2021"
        );
        const blocks = await res.json();
        // const dataPanelone = blocks.panelone;
        // const dataPaneltwo = blocks.paneltwo;

        // this.setState({
        //   panelone: dataPanelone,
        //   paneltwo: dataPaneltwo,
        // })
        // alert("elll");

        console.log(blocks.centers.length);
        counter = counter + 1;
        
        if (blocks.centers.length > 0 || counter == 1) {
          var notification = new Notification("Book cowin", {
            icon: null,
            body: "Book cowin",
          });
          alert("Notified");
        }

        //Here will pop notifier and open in a specified name window "popwin1" when clicked.

        //Here will pop notifier and focus parent window only when clicked.
      }, 5000);
    } catch (e) {
      console.log(e);
    }
  }
}

export default CowinAlert;
